"use client";
import { useState } from "react";
import { useRouter } from "next/navigation";
import api from "@/lib/api";
import Form from "@/components/form";

export default function NovoCliente() {
  const [cliente, setCliente] = useState({
    codigo: "",
    nome: "",
    fantasia: "",
    documento: "",
    endereco: "",
  });

  const router = useRouter();

  const handleChange = (e) => {
    setCliente({ ...cliente, [e.target.name]: e.target.value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const token = localStorage.getItem("token"); // Pegando o token salvo no login
      if (!token) {
        alert("Usu√°rio n√£o autenticado");
        return;
      }

      await api.post(
        "/clientes",
        {
          ...cliente,
          usuarioNome: "-", // Enviando o campo obrigat√≥rio
        },
        {
          headers: {
            Authorization: `Bearer ${token}`, // Adicionando o token no header
          },
        }
      );

      router.push("/clientes");
    } catch (error) {
      console.error("Erro ao criar cliente:", error.response?.data || error);
      alert("Erro ao criar cliente. Veja o console.");
    }
  };

  const fields = [
    { name: "codigo", label: "C√≥digo", required: true },
    { name: "nome", label: "Nome", required: true },
    { name: "fantasia", label: "Nome Fantasia" },
    { name: "documento", label: "Documento (CPF/CNPJ)", required: true },
    { name: "endereco", label: "Endere√ßo" },
  ];

  return <Form title="üÜï Criar Cliente" fields={fields} values={cliente} onChange={handleChange} onSubmit={handleSubmit} />;
}
